@include tablet {
    .iconos-nosotros {
        @include grid(3, 2rem);
    }
}
.icono {
    text-align: center;
    img {
        width: 30%;
        height: 15%;
        border-radius: .5rem;
        box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
        margin: auto;    
        @include tablet {
            width: 60%;
            height: 25%;
        }
    }

    h3 {
        text-transform: uppercase;
    }
}