.formulario {
    
    p {
        font-size: 1.4rem;
        color: $azulOscuro;
        margin: 2rem 0 0 0;
    }

    legend {
        font-size: 2rem;
        color: $azulOscuro;  
              
    }

    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
        
    }
    
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: .5rem;
        margin-bottom: 2rem;
    }
    textarea {
        height: 20rem;
        max-width: 116.6rem;
    }
    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    } 
}