.dark-mode {
    background-color: darken($grisOscuro, 10) ;

    p {
        color: $blanco;
    }

    a, h1, h2, h3, h4 {
        color: $gris;
    }

    .anuncio,
    .resumen-propiedad {
        background-color: $grisOscuro;
        border: .1rem solid $grisOscuro;
        box-shadow: 0px 8px 10px 0px rgba( 58, 57, 57 , .3);

        .precio {
            color: $verde;
        }

        .icono {
            filter: invert(100%);
        }
    }
    .texto-nosotros blockquote {
        color: $blanco;
    }

    .formulario {
        label,
        legend,
        p {
            color: $gris;
        }
        input:not([type="submit"]),
        textarea,
        select {
            background-color: $grisOscuro;
            border: .1rem solid $grisOscuro;
            color: $blanco;
        }
    }

}