.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: flex-end;
    }

    .dark-mode-boton {
      width: 6rem;
      margin-top: 3rem;
      margin-bottom: 2rem;
      transition: all .3s ease-in-out;
      
      &:hover {
        filter: invert(100%);
        cursor: pointer; 
        transform: scale(1.2); 
      }

      @include tablet {
          margin-top: 0;
      }
    }
}

.barra {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    align-items: center;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    } 
    
    & img {
        height: 6rem;
    }

}

.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .5s ease-in-out, opacity 0.5s linear, height .5s linear;
    

    @include tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    a {
        color: $blanco;        
        font-size: 2.2rem;
        display: block;
        text-align: center;
        padding: .5rem;         
        transition: all .3s ease ;
        &:hover{                                 
            color: $amarillo;           
            
        }  
        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin-right: 0;
            }
            
        }

     
    }
}

/*****Menú de Hambuerguesa*******/
.mobile-menu {
    @include tablet {
        display: none;
    }

    img {
        margin-top: 2rem;
        width: 5rem;
        transition:all .3s ease-in-out;

        &:hover {
            filter: opacity(.5);       
            cursor: pointer;
            transform: scale(.9);            
        }
    }


}
.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;

}